import React from "react"
import { Card, CardBody, CardTitle, Badge, Button } from "reactstrap"
import { Link } from "react-router-dom"

const AllLeads = () => {
  const leads = [
    {
      id: "customCheck2",
      leadCName: "Mohit Grover",
      dateFirst: "5/1/2020",
      dateLast: "5/4/2020",
      email: "mohit@esimplified.ca",
      phone: "+1 1234567890",
      badgeClass: "danger",
      leadStatus: "Open-Follow up",
      link: "#",
    },
    {
      id: "customCheck3",
      leadCName: "Vibha Grover",
      dateFirst: "5/1/2020",
      dateLast: "5/4/2020",
      email: "vibha@esimplified.ca",
      phone: "+1 1234567890",
      badgeClass: "success",
      leadStatus: "Open-New",
      link: "#",
    },
   
  ]

  return (
    <React.Fragment>
      <Card>
        <CardBody>
         
          <div className="table-responsive">
            <table className="table align-middle table-nowrap mb-0">
              <thead className="table-light">
                <tr>
                  <th style={{ width: "20px" }}>
                    <div className="form-check font-size-16 align-middle">
                      <input
                        type="checkbox"
                        className="form-check-input"
                        id="customCheck1"
                      />
                      <label
                        className="form-check-label"
                        htmlFor="customCheck1"
                      >
                        &nbsp;
                      </label>
                    </div>
                  </th>
                  <th className="align-middle">Name</th>
                  <th className="align-middle">Date / Last </th>
                  <th className="align-middle">Contact</th>
                  <th className="align-middle">Status</th>
                 
                  <th className="align-middle">View Details</th>
                </tr>
              </thead>
              <tbody>
                {leads.map((lead, key) => (
                  <tr key={"_tr_" + key}>
                    <td>
                      <div className="form-check font-size-16">
                        <input
                          type="checkbox"
                          className="form-check-input"
                          id={lead.id}
                        />
                        <label
                          className="form-check-label"
                          htmlFor={lead.id}
                        >
                          &nbsp;
                        </label>
                      </div>
                    </td>
                    <td>
                      <Link to="#" className="text-body fw-bold">
                        {" "}
                        {lead.leadCName}{" "}
                      </Link>{" "}
                    </td>
                    <td>{lead.dateFirst} - {lead.dateLast}</td>
                    <td>{lead.email} - {lead.phone}</td>
                    
                    <td>
                      <Badge
                        className={
                          "font-size-11 badge-soft-" + lead.badgeClass
                        }
                        color={lead.badgeClass}
                        pill
                      >
                        {lead.leadStatus}
                      </Badge>
                    </td>
                   
                    <td>
                        <Link to="/lead/lead-detail" className="btn-primary waves-effect waves-light btn-sm">View Details</Link>
                     
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </CardBody>
      </Card>
    </React.Fragment>
  )
}

export default AllLeads
